export default {
	data() {
		return {
			list:[
				{
				selected:true,text:"男"
			},
			{
				selected:false,text:"女"
			}
			]
		}
	},
	methods: {
		selected(index){
			this.list.map(v=>{
				return v.selected = false
			})
			this.list[index].selected= true
		}
	}
}
